import * as f from "../functions.js"

export class basicAttackCardClass{
static generatedCardType = "attack"

  constructor(damage,energyCost){
    this.damage = damage
    this.energyCost = energyCost
    


    this.target = "enemy"
    this.type = "attack"
  }
  defaultPlay(enemy){
    console.log(this.name , "attacked" , enemy.name , "for" , this.damage);
    f.damage(enemy , this.damage)
    this.discard()
  }
  discard(){
    f.discardCard(this)
  }
  attack(enemy){
    f.damage(enemy , this.damage)
  }
}

export class strike extends basicAttackCardClass {
  constructor(damage,energyCost){
    super(7 , 1)

    this.name = "Strike"

  }
  play(enemy){
    this.defaultPlay(enemy)
  }
}

export class bash extends basicAttackCardClass{
  constructor(damage,energyCost){
    super(10 , 2) 

    this.name = "Bash"

  }

  play(enemy){
    this.defaultPlay(enemy)
  }
}


